@import "variables";
.education {
  li {
    // transition: all .3s ease;
    // &:hover {
    //   transform: scale(1.01);
    // }
    box-shadow: $box-shadow;
    margin: 1em 0;
    display: flex;
    .left {
      min-height: 10em;
      width: 5%;
      min-width: 3.3em;
      background-color: $accent;
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-direction: column;

      svg {
        // align-self: cen;
        margin: 0.7em;
        path {
          color: $text-bg;
        }
      }
      div.dates {
        // align-self: flex-end;
        font-size: 0.7em;
        text-align: center;
        // font-weight: bold;
        font-weight: lighter;
        padding: 1em;

        span {
          color: $text-bg;
        }
        hr {
          border: none;
          border-bottom: 0.1em solid $text-bg;
        }
      }
    }
    .right {
      width: 100%;
      background-color: $text-bg;
      padding: 1em;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .content {
        align-self: flex-start;
        width: 100%;
      }
      .foot {
        width: 100%;
        background-color: $text-bg;
        display: flex;
        font-size: 0.8em;
        text-align: center;
        font-weight: bold;
        justify-content: flex-start;
        align-items: flex-start;
        // align-self: flex-end;
        flex-direction: column;
        // padding: 1em;
        span {
          color: #9a9a9a;
          font-weight: lighter;
        }
      }
    }
  }
}
